.Players {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    overflow: auto;
    margin-bottom: 1.5rem;
    height: 100%;
}

/* PLAYER */

.PlayerContainer {
    width: 30rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex: 0 0 2rem;
        flex: 0 0 2rem;
    -ms-flex-align: center;
        align-items: center; 
    border: 1px solid transparent; 
    -webkit-transition: all .2s; 
    -o-transition: all .2s; 
    transition: all .2s;
    border-radius: 3px;

}

.PlayerContainer:not(:last-child) {
    margin-bottom: .5rem;
}

.PlayerContainer:hover {
    border: 1px solid #eee;
    -webkit-box-shadow: 0 1px 1rem rgba(0, 0 , 0, .2);
            box-shadow: 0 1px 1rem rgba(0, 0 , 0, .2);
}


.Player {
    -ms-flex-positive: 1;
        flex-grow: 1;
    display: block;
    background-color: transparent;
    border: none;
    color: rgb(40, 40, 255);
    font-family: inherit;
    font-size: inherit;
    font-weight: 700;
    outline: none;
    padding: 1rem;
    text-transform: uppercase;
    cursor: pointer;
}

.Player:hover{
    color: rgb(0, 185, 185);
}




