.Board {
    width: 100%;
    height: 95%;
    margin: 0px;
}
.Modal {
    padding-bottom: 60px;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1.6rem;
}

.Modal p{
  margin-bottom: 1.5rem;
}

.Buttons {
    display: -ms-flexbox;
    display: flex;
}

@media only screen and (max-width: 600px) {
    .Modal{
        font-size: 1.3rem;
    }
    .Buttons {
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
    }
}


